@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  box-sizing: border-box; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  color: #ffffff; }

main {
  flex-grow: 1; }

header {
  flex-grow: 0; }

footer {
  flex-grow: 0; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none !important; }

.container {
  flex: 1 0 100%;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 15px;
  width: 1200px;
  max-width: 100%;
  min-height: 100%;
  background-color: #C0DBD9; }

.header {
  padding: 15px 30px;
  min-height: 100px;
  display: flex;
  align-items: center;
  background-color: #7FAFF6; }

.header__btn {
  display: block;
  padding: 8px 15px;
  font-size: 20px;
  line-height: 1.1;
  color: #ffffff;
  background-color: #A5C7F9;
  border-radius: 5px; }
  .header__btn:hover, .header__btn:focus, .header__btn:active {
    cursor: pointer;
    opacity: 0.6; }
  @media screen and (max-width: 550px) {
    .header__btn {
      margin: 0 auto; } }

.footer {
  min-height: 100px;
  background-color: #7FAFF6; }

.main__block {
  margin: 15px 0; }

.block-1 {
  counter-reset: item;
  display: grid;
  grid-gap: 15px;
  grid-template-columns: 390px 1fr 160px;
  background-color: #B3F95A; }
  @media screen and (max-width: 900px) {
    .block-1 {
      grid-template-columns: 1fr; } }

.block-1__item {
  position: relative;
  height: 198px;
  background-color: #8AD191; }
  .block-1__item::before {
    counter-increment: item;
    content: counter(item);
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 53px;
    line-height: 1;
    transform: translate(-50%, -50%); }
  .block-1__item:nth-child(5n+4) {
    grid-column: 1/3; }
    @media screen and (max-width: 900px) {
      .block-1__item:nth-child(5n+4) {
        grid-column: 1/2; } }

.block-2 {
  display: grid;
  grid-column-gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  padding: 30px;
  background-color: #88B5BF; }

.block-2__item {
  height: 0;
  padding-bottom: 78%;
  background-color: #0D89B0; }

.popup {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 2;
  top: -150%;
  left: 0;
  width: 100vw;
  height: 100vh;
  transition: all 1s ease-in; }
  .popup:target {
    top: 0;
    transition: all 1s ease-in; }

.popup__content {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 400px;
  max-width: 100%;
  height: 400px;
  max-height: 100%;
  overflow-y: auto;
  background-color: #ffffff; }

.popup__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 15px;
  top: 15px;
  width: 43px;
  height: 39px;
  background-color: #7FAFF6;
  border-radius: 5px; }
  .popup__btn:hover, .popup__btn:focus, .popup__btn:active {
    cursor: pointer;
    opacity: 0.6; }

.popup__text {
  margin: 0;
  font-size: 53px;
  line-height: 1.1;
  color: #000000; }
